import React from 'react';
import DocumentTitle from 'react-document-title';
import Picker from 'src/components/Picker';
import Button from 'src/components/Button';
import addressList from '../addressList';

const list1 =
    [{
        id: 1,
        name: ''
    }, {
        id: 2,
        name: ''
    }, {
        id: 3,
        name: ''
    }, {
        id: 4,
        name: ''
    }, {
        id: 5,
        name: ''
    }, {
        id: 6,
        name: ''
    }, {
        id: 7,
        name: ''
    }, {
        id: 8,
        name: ''
    }, {
        id: 9,
        name: ''
    }, {
        id: 10,
        name: ''
    }];
const list2 = [
    [{
        id: 1,
        name: ''
    }, {
        id: 2,
        name: ''
    }, {
        id: 3,
        name: ''
    }, {
        id: 4,
        name: ''
    }, {
        id: 5,
        name: ''
    }, {
        id: 6,
        name: ''
    }, {
        id: 8,
        name: ''
    }],
    [{
        'id': 1,
        'name': ''
    }, {
        'id': 1,
        'name': ''
    }, {
        'id': 1,
        'name': ''
    }]
];
const ButtonPickerGroup = React.createClass({
    getInitialState: function() {
        return {
            openPicker: false,
            selectedValue: ''
        };
    },
    getDefaultProps: function() {
        return {
            colorStyle: 'primary'
        }
    },
    render: function() {
        let {
            openPicker,
            selectedValue
        } = this.state;
        let buttonClass = 'btn-' + this.props.colorStyle;
        return (
            <div>
                <Button className={buttonClass} block={true} style={{'marginLeft': 0}} onClick={() => {this.setState({openPicker: true})}}>{this.props.children}</Button>
                <p>{selectedValue}</p>
                <Picker 
                    {...this.props}
                    isPickerOpen={openPicker} 
                    onClose={()=>{
                        // something you wanna do when scroll picker is closed
                        this.setState({
                            openPicker: false
                        })
                    }}
                    onSelected={(value) => {
                        // The return value is organised like [{id: 1, name: 'foo'},...]

                        // You must set the openPicker to false, otherwise the scroll picker
                        // won't shut down himself!!
                        // TODO:: how to shut down himself ??
                        this.setState({
                            selectedValue: value.reduce((pre, cur)=> {
                                return pre + ' ' + cur.name;
                            }, ''),
                            openPicker: false
                        })
                    }} 
                ></Picker>
            </div>
        );
    }
});

const PickerPage = React.createClass({
    getInitialState: function() {
        return {
            index: [0]
        };
    },
    componentDidMount: function() {

    },
    render: function() {
        let marginBottom = {
            marginBottom: '40px'
        };
        return (
            <DocumentTitle title="Picker">
                <div>
                    <div style={marginBottom}>
                        <p></p>
                        <ButtonPickerGroup optionsGroup={list1} selectedIndexGroup={[1]}></ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={list2} selectedIndexGroup={[3,2]}></ButtonPickerGroup>
                    </div>
                    <div  style={marginBottom}>
                        <p></p>
                        <Button 
                            onClick={()=>{this.setState({
                                index: [parseInt(Math.random() * 10)]
                            })}}
                        >  </Button> {list1[this.state.index[0]].name}
                        <ButtonPickerGroup optionsGroup={list1} selectedIndexGroup={this.state.index}></ButtonPickerGroup>
                    </div>
                    <div style={marginBottom}>
                        <p></p>
                        <ButtonPickerGroup optionsGroup={addressList} isCascading={true} columnNum={2} ></ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={addressList} isCascading={true} columnNum={3} ></ButtonPickerGroup>
                    </div>
                    <div style={marginBottom}>
                        <p>()</p>
                        <ButtonPickerGroup optionsGroup={list1}>5</ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={list1} lineNum={3}>3</ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={list1} lineNum={4}>43</ButtonPickerGroup>
                    </div>
                    <div style={marginBottom}>
                        <p></p>
                        <ButtonPickerGroup optionsGroup={list1} colorStyle="success">success</ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={list1} colorStyle="warning">3</ButtonPickerGroup>
                        <ButtonPickerGroup optionsGroup={list1} colorStyle="alert">43</ButtonPickerGroup>
                    </div>
                </div>
            </DocumentTitle>
        );
    }
});

export default PickerPage;


